import { defineComponent, PropType, toRefs } from 'vue';
import { JSX } from 'vue/jsx-runtime';

import cn from 'classnames';

import SvgIcon from '../svgIcon/SvgIcon';

import styles from './empty.module.less';

export default defineComponent({
  name: 'Empty',
  props: {
    icon: {
      type: Object as PropType<JSX.Element | string>,
    },
    title: {
      type: [String, Object] as PropType<JSX.Element | string>,
      default: 'ÊöÇÊó†Êï∞ÊçÆ'
    },
    description: {
      type: [String, Object] as PropType<JSX.Element | string>,
      default: ''
    }
  },
  setup(props) {
    const { icon, title, description } = toRefs(props);

    const renderIcon = () => {
      if (icon.value) {
        return icon.value;
      }
      return <>
        {/* üà≥Ô∏è */}
        <SvgIcon name="vacancy" />
      </>;
    };

    return () => (<div class={cn(styles.empty)}>
      <div class={cn(styles.logo)}>
        {renderIcon()}
      </div>
      <div class={cn(styles.title)}>
        {title.value}
      </div>
      <div class={cn(styles.description)}>
        {description.value}
      </div>
    </div>);
  }
});

